body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}
*{
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.state-message {
  text-align: center;
  font-size: 13px;
  font-weight: bold;
}

.App {
  width: 100vw;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  height: 100vh;
  background-color: #292a2c;
  background-image: url('./images/bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;

}

.sidebar {
  font-size: 14px;
}

.connect-btn{
  cursor: pointer;
  display: inline-block;
  opacity: 1;
  background-color: transparent;
  box-shadow: none;
  border-radius: 10px;
  height: 38px;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-transform: none;
  min-width: 64px;
  padding: 5px 50px;
  border-radius: 10px;
  border-width: 1px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
}
.connect-btn:hover{
  background-color: #000000;
  color: #fff;
}

.chat {
  flex: 1;
  display: flex;
  flex-direction: column;
  margin: 60px auto;
  width: 700px;
  height: 100%;
  overflow: hidden;
  background-color: #fff;
}
.chat-header{
  height: 70px;
  width: 100%;
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
  color: #fff;
  padding-left: 20px;
  padding-top: 20px;
}
.chat-header .kefuName{
  font-size: 14px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: normal;
    color: #fff;
}

.chat-header .desc{
  white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    margin-top: 8px;
    font-size: 12px;
    line-height: 1;
    color: hsla(0,0%,100%,.75);
}

.chat__messages {
  overflow: visible auto;
  flex: 1;
  padding-bottom: 20px;
  border-top-left-radius: 20px;
}

.chat__actions-wrapper {
  height: 150px;
  width: 100%;
  overflow: hidden;
  position: relative;
  background-color: #fff;
  border-top: 1px solid #ddd;
}

.chat__input {
  width: 100%;
  height: 56px;
  padding: 10px 15px 0;
  border-radius: 2px;
  line-height: 20px;
  font-size: 16px;
  background-color: transparent;
  resize: none;
  border: none;
  outline: none;
}
.chat__input:disabled{
  cursor: not-allowed;
  opacity: 0.6;
}

.sendLineWrap {
  position: absolute;
  bottom: 15px;
  right: 15px;
  text-align: right;
  line-height: 0;
  font-size: 0;
  display: inline-block;
  cursor: pointer;
  padding: 0 18px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  border-radius: 2px;
  background-color: #00c3cc;
  font-size: 12px;
  color: #fff;
  outline: none;
  border: none;
}
.sendLineWrap:disabled{
  cursor: not-allowed;
  opacity: 0.6;
}

textarea {
  width: 100%;
}

.chat__row {
  margin: 20px 10px;
}

.chat__bubble {
  display: flex;
}

.chat__bubble.left {
  justify-content: start;
}

.chat__bubble.right {
  justify-content: end;
}

.chat__message {
  border-radius: 5px;
  display: inline-block;
  padding: 10px 18px;
  position: relative;
  margin-top: 10px;
  max-width: 50%;
}

.chat__message:before {
  content: "\00a0";
  display: block;
  height: 16px;
  width: 9px;
  position: absolute;
  bottom: -7.5px;
}

.chat__message.left {
  background-color: #e6e5eb;
}

.chat__message.left:before {
  background-color: #e6e5eb;
  left: 10px;
  -webkit-transform: rotate(70deg) skew(5deg);
}

.chat__message.right {
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
  color: #fff;
}

.chat__message.right:before {
  background-image: linear-gradient(-225deg, #3D4E81 0%, #5753C9 48%, #6E7FF3 100%);
  right: 10px;
  -webkit-transform: rotate(118deg) skew(-5deg);
}

.header{
  padding: 0 40px;
  line-height: 64px;
  text-align:  right;
  height: 64px;
  background-color: #fff;
  opacity: 0.8;
}

.dapp{
  display: flex;
  flex-direction: column;
  flex: 1;
  overflow: hidden;
}

@media only screen and (max-width: 768px) {
  .chat{
    width: 100%;
    height: 100%;
    margin: 0 auto;
  }
  .header{
    height: 0;
    padding: 0 16px;
    
  }
  .connect-btn {
    padding: 4px 16px;
    border: 2px solid #fff;
    color: #fff;
  }
}

